import { connect } from 'react-redux'
import Amount from '../components/Amount.js'
import { updateHomeValue } from '../actions'

const mapStateToProps = (state) => {
  return {label: "Home Value"}
}

const mapDispatchToProps = (dispatch) => {
  return {
    afterUpdate: (val) => {
      dispatch(updateHomeValue(val))
    }
  }
}

const HomeValueUpdate = connect(
  mapStateToProps,
  mapDispatchToProps
)(Amount)

export default HomeValueUpdate


