import { connect } from 'react-redux'
import Amount from '../components/Amount.js'
import { updateBudgetPercent } from '../actions'

const mapStateToProps = (state) => {
    return {label: "Budget Percent", amount: state.budget.budgetPercent, type: "percent"}
}

const mapDispatchToProps = (dispatch) => {
  return {
    afterUpdate: (val) => {
      dispatch(updateBudgetPercent(val))
    }
  }
}

const BudgetPercentUpdate = connect(
  mapStateToProps,
  mapDispatchToProps
)(Amount)

export default BudgetPercentUpdate


