import React, { PropTypes } from 'react'

class Amount extends React.Component {
    constructor(props) {
        super(props);
        let hv = (props.amount)? props.amount : "";
        this.state = {timer: null, hv: hv};
    }

    componentWillReceiveProps(nextProps) {
        this.setState((prevState, props) => {
            return Object.assign({}, prevState, {hv: props.amount})
        })
    }

    asNumber(val) {
        if(typeof val !== "string") return Math.floor(val)
        let newVal = val.replace(/[^0-9.]/g, "")
        let parts = newVal.split(".")
        if (parts[0] == "") return 0;
        return parseInt(parts[0])
    }

    clean(val) {
        let newVal = this.asNumber(val)
        if (newVal == 0) return ""
        return new Intl.NumberFormat().format(newVal)
    }

    handleOnChange = (proxy) => {
        let timer = null;
        let currentValue = proxy.target.value;
        this.props.afterUpdate(this.asNumber(currentValue));
        if(this.state.timer) {
            clearTimeout(this.state.timer);
        }
        timer = setTimeout(() => {
            let newHV = this.state.hv;
            this.setState((prevState, props) => {
                return {timer: null, hv: newHV};
            })
        }, 500);
        this.setState((prevState, props) => {
            return {timer: timer, hv: currentValue}
        })
    }

    render() {
        let label = ""
        let amountAddon = ""
        let percentAddon = ""
        if(this.props.label) {
            label = <label htmlFor="homevalue">{this.props.label}</label>
        }
        if(!this.props.type || this.props.type == "amount") {
            amountAddon = <span className="input-group-addon">$</span>
        }
        if(this.props.type && this.props.type == "percent") {
            percentAddon = <span className="input-group-addon">%</span>
        }
        return (
            <div className="form-group">
                {label}
                <div className="input-group">
                    {amountAddon}
                    <input type="text" className="form-control" name="homevalue" value={this.clean(this.state.hv)} onChange={this.handleOnChange} />
                    {percentAddon}
                </div>
            </div>
        )
    }
}

export default Amount


