import React from 'react';
import { connect } from 'react-redux'
import Amount from '../components/Amount.js'
import { updateBudgetPercent } from '../actions'

class BudgetItem extends React.Component {
    constructor(props) {
        super(props)
        this.state = {amount: props.budgetAmount * props.percent / 100, percent: props.percent}
    }

    afterUpdate = (value) => {
        this.setState((prevState, props) => {
            return Object.assign({}, prevState, {
                percent: value,
                amount: props.budgetAmount * value / 100
            })
        })
    }

    componentWillReceiveProps(nextProps) {
        this.setState((prevState, props) => {
            return Object.assign({}, prevState, {
                amount: nextProps.budgetAmount * this.state.percent / 100
            })
        })
    }

    render() {
        let donothing = () => {}
        return (
            <div className="row">
                <div className="col col-md-6 col-sm-4">
                    <div className="form-group">
                        <div className="form-control-static">{this.props.title}</div>
                    </div>
                </div>
                <div className="col col-md-2 col-sm-4">
                    <Amount type="percent" amount={this.state.percent} afterUpdate={this.afterUpdate} />
                </div>
                <div className="col col-md-4">
                    <Amount type="amount" amount={this.state.amount} afterUpdate={donothing} />
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {budgetAmount: state.budget.budgetAmount}
}

const mapDispatchToProps = (dispatch) => {
  return {
    afterUpdate: (val) => {
      dispatch(updateBudgetPercent(val))
    }
  }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(BudgetItem)


