
const initialState = {homeValue: 0, budgetPercent: 15, budgetAmount: 0, lastSet: "percent"}
export function budgetReducer (state = initialState, action) {
    switch (action.type) {
        case "UPDATE_HOME_VALUE":
            let out = Object.assign({}, state, {
                homeValue: action.value
            })
            if(state.lastSet == "percent") {
                out.budgetAmount = action.value * state.budgetPercent / 100
            }else{
                out.budgetPercent = Math.round(state.budgetAmount / action.value * 100)
            }
            return out
        case "UPDATE_BUDGET_PERCENT":
            return Object.assign({}, state, {
                budgetPercent: action.percent,
                lastSet: "percent",
                budgetAmount: state.homeValue * action.percent / 100
            })
        case "UPDATE_BUDGET_AMOUNT":
            return Object.assign({}, state, {
                budgetAmount: action.amount,
                lastSet: "amount",
                budgetPercent: Math.round(action.amount / state.homeValue * 100)
            })
        case "UPDATE_BUDGET_ITEM":
            let update = {}
            update[action.key] = action.value
            return Object.assign({}, state, update)
        default:
            return state
    }
}


