import { connect } from 'react-redux'
import Amount from '../components/Amount.js'
import { updateBudgetAmount } from '../actions'

const mapStateToProps = (state) => {
    return {label: "Budget Amount", amount: state.budget.budgetAmount}
}

const mapDispatchToProps = (dispatch) => {
  return {
    afterUpdate: (val) => {
      dispatch(updateBudgetAmount(val))
    }
  }
}

const BudgetAmountUpdate = connect(
  mapStateToProps,
  mapDispatchToProps
)(Amount)

export default BudgetAmountUpdate


