import React from 'react'
import './HomeView.scss'
import HomeValueUpdate from '../../../containers/HomeValueUpdate.js'
import BudgetAmountUpdate from '../../../containers/BudgetAmountUpdate.js'
import BudgetPercentUpdate from '../../../containers/BudgetPercentUpdate.js'
import BudgetItem from '../../../containers/BudgetItem.js'

export const HomeView = () => (
  <div>
    <section className="home">
      <h3>Renovation Project Budget Calculator</h3>
      <div className="row">
        <div className="col col-md-9">
          <p><br />Determine a total kitchen or bath remodeling budget by selecting a percentage of your home's value.</p>
        </div>
        <div className="col col-md-3">
          <HomeValueUpdate />
        </div>
      </div>
      <div className="row">
        <div className="col col-md-9">
          <p>A kitchen project is a defining room in your home.  Consider beginning with 15%  as a starting point for a kitchen and 8%  for a master bath project.<br />Remember that a major kitchen remodel project adds 72.1% of its value back into your home's value and a bathroom remodel adds 71% of its value based on a national average.* <br /><small>(*Source:  2009-10 Cost vs. Value, Remodeling Magazine.)</small></p>
        </div>
        <div className="col col-md-3">
          <BudgetPercentUpdate />
        </div>
      </div>
    </section>
    <section className="budget">
      <h3>Your Budget Breakdown</h3>
      <div className="row">
        <div className="col col-md-4 offset-8">
          <BudgetAmountUpdate />
        </div>
      </div>
      <BudgetItem title="Cabinets" percent={40} />
      <BudgetItem title="Countertops" percent={12} />
      <BudgetItem title="Flooring" percent={7} />
      <BudgetItem title="Appliances" percent={8} />
      <BudgetItem title="Electrical" percent={4} />
      <BudgetItem title="Wall Covering" percent={3} />
      <BudgetItem title="Labor" percent={25} />
      <BudgetItem title="Extra Expenses" percent={1} />
    </section>
  </div>
)

export default HomeView



