export const updateHomeValue = (value) => {
    return {
        type: "UPDATE_HOME_VALUE",
        value: value
    }
}

export const updateBudgetAmount = (amount) => {
    return {
        type: "UPDATE_BUDGET_AMOUNT",
        amount: amount
    }
}

export const updateBudgetPercent = (percent) => {
    return {
        type: "UPDATE_BUDGET_PERCENT",
        percent: percent
    }
}


